.header {
  text-align: center;
  padding: 50px;
  background: #000000; /* Solid black background */
  color: #ffffff; /* White text for contrast */
  border-radius: 15px;
  box-shadow: 0px 4px 15px rgba(255, 255, 255, 0.1); /* Subtle glow effect */
}

.photo {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 4px solid #ffffff; /* White border for contrast */
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.photo:hover {
  transform: scale(1.1); /* Slight zoom-in effect on hover */
}

h1 {
  font-size: 3.2rem; /* Slightly larger font for emphasis */
  margin-bottom: 15px;
  letter-spacing: 1px;
  font-family: 'Arial Black', sans-serif; /* Bold, impactful font */
  text-transform: uppercase;
  color: #ffde59; /* Soft yellow for name highlight */
}

h1 .highlight {
  font-weight: bold;
  color: #ff6700; /* Subtle orange highlight */
}

h2 {
  font-size: 1.6rem;
  margin-bottom: 20px;
  font-style: italic;
  font-family: 'Roboto', sans-serif; /* Clean, modern font */
  color: #c5c6c7; /* Light grey for a soft contrast */
}

.description {
  font-size: 1.2rem;
  margin-bottom: 30px;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
  color: #a5a5a5; /* Softer grey tone */
}

.social-links a {
  margin: 0 10px;
  color: #ffffff; /* White text for buttons */
  text-decoration: none;
  font-size: 1rem;
  border: 2px solid #ffffff; /* White outline for buttons */
  padding: 10px 20px;
  border-radius: 25px;
  background: transparent; /* Transparent button for a sleek look */
  transition: all 0.3s ease;
}

.social-links a:hover {
  background: #ffffff; /* White background on hover */
  color: #000000; /* Black text on hover for contrast */
  border-color: #ffde59; /* Matches the name's highlight color */
  transform: translateY(-3px); /* Lift effect on hover */
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.2); /* Glow effect */
}
